@mixin footer-bottom--mobile {
  @include mobile {
    margin-top: $lem * 2;

    @include grid(1, 0px, $lem);

    > * {
      min-height: $lem * 2;
    }
    
    flex-direction: column;
    justify-content: center;

    .sprite {
      display: flex;
      justify-content: center;
    }

    .icon {
      text-align: center;
    }
  }
}
